'use server';
import { syncPlaylistTask } from '@/trigger/sync-playlist-task';
import { schedules } from '@trigger.dev/sdk/v3';

const getCronPerFrequency = (frequency: 'minute' | 'hour' | 'day' | 'week') => {
	switch (frequency) {
		case 'minute':
			return '* * * * *';
		case 'hour':
			return '0 * * * *';
		case 'day':
			return '0 0 * * *';
		case 'week':
			return '0 0 * * 1';
	}
};
export async function addCron(
	playlistId: string,
	frequency: 'minute' | 'hour' | 'day' | 'week'
) {
	await schedules.create({
		task: syncPlaylistTask.id,
		cron: getCronPerFrequency(frequency),
		deduplicationKey: `playlistId:${playlistId}`,
	});
}
